import { tmsPosList } from './pos_info.js'
function addRows(hot, data, row, type) {
  if (row == -1) { //
    row = 0; /*  */
  }
  hot.alter('insert_row', row);
  setTimeout(function(){
    hot.view.wt.wtTable.draw();//
  })
  // setTimeout(function(){
  //   hot.render();//
  // });
  reRenderSelectOption(hot, data, type, row, true);
}


function reRenderSelectOption(hot, data, type, newRow, isnew) { //selectOption,option
  let cellMeta = hot.getCellMetaAtRow(0);
  let rows = data.length - 1;
 
  cellMeta.forEach(item => {
    if (item.prop == "t_name") { //
      if (isnew) { // 
        hot.setCellMeta(newRow,item.col,"readOnly",false);//
        hot.setCellMeta(newRow, item.col, "selectOptions", row => { return data[hot.toPhysicalRow(row)].t_sources })
      } else {
        for (let i = 0; i < rows; i++) { //hot 
          hot.setCellMeta(i, item.col, "selectOptions", row => { return data[hot.toPhysicalRow(row)].t_sources })
        }
      }
      return
    }
    if (item.prop == "mac_me") { //
      if (isnew) { //
        hot.setCellMeta(newRow,item.col,"readOnly",false);//
        hot.setCellMeta(newRow, item.col, "selectOptions", row => { return data[hot.toPhysicalRow(row)].macName_sources })
      } else {
        for (let i = 0; i < rows; i++) {
          hot.setCellMeta(i, item.col, "selectOptions", row => { return data[hot.toPhysicalRow(row)].macName_sources })
        }
      }
      return
    }
    if (item.prop == "dgm_name") { //
      if (isnew) { //
        hot.setCellMeta(newRow,item.col,"readOnly",false);//
        hot.setCellMeta(newRow, item.col, "selectOptions", row => { return data[hot.toPhysicalRow(row)].dgm_sources })
      } else {
        for (let i = 0; i < rows; i++) {
          hot.setCellMeta(i, item.col, "selectOptions", row => { return data[hot.toPhysicalRow(row)].dgm_sources })
        }
      }
      return
    }
    if (item.prop == "ch_name") { //
      if (isnew) { //
        hot.setCellMeta(newRow, item.col, "selectOptions", row => { return data[hot.toPhysicalRow(row)].ch_sources })
      } else {
        for (let i = 0; i < rows; i++) {
          hot.setCellMeta(i, item.col, "selectOptions", row => { return data[hot.toPhysicalRow(row)].ch_sources })
        }
      }
      return
    }
    if (item.prop == "task_id_val") { //
      if (isnew) { //
        hot.setCellMeta(newRow, item.col, "selectOptions", row => { return data[hot.toPhysicalRow(row)].task_id_sources })
      } else {
        for (let i = 0; i < rows; i++) {
          hot.setCellMeta(i, item.col, "selectOptions", row => { return data[hot.toPhysicalRow(row)].task_id_sources })
        }
      }
      return
    }
    if (item.prop == "baseInfo.frequency_val") { //
      if (isnew) { //
        hot.setCellMeta(newRow, item.col, "selectOptions", row => { return data[hot.toPhysicalRow(row)].baseInfo.frequency_sources })
      } else {
        for (let i = 0; i < rows; i++) {
          hot.setCellMeta(i, item.col, "selectOptions", row => { return data[hot.toPhysicalRow(row)].baseInfo.frequency_sources })
          // hot.setCellMeta(i,item.col,"selectOptions",row=>{ return  ['12.5','14',4.4]})
        }
      }
      return
    }
    if (item.prop == "baseInfo.cc_mod_id_val") { //
      if (isnew) { //
        hot.setCellMeta(newRow, item.col, "selectOptions", row => { return data[hot.toPhysicalRow(row)].baseInfo.cc_mod_id_sources })
      } else {
        for (let i = 0; i < rows; i++) {
          hot.setCellMeta(i, item.col, "selectOptions", row => { return data[hot.toPhysicalRow(row)].baseInfo.cc_mod_id_sources })
        }
      }
      return
    }
    if (item.prop == "baseInfo.spectral") { //
      if (isnew) { //
        hot.setCellMeta(newRow, item.col, "selectOptions", row => { return data[hot.toPhysicalRow(row)].baseInfo.spectral_sources })
      } else {
        for (let i = 0; i < rows; i++) {
          hot.setCellMeta(i, item.col, "selectOptions", row => { return data[hot.toPhysicalRow(row)].baseInfo.spectral_sources })
        }
      }
      return
    }

     /*  */
    if (type == 1) {
      if (item.col >= 8) {//
        for (let i = 0; i < rows; i++) { 
          let rowNum = hot.toPhysicalRow(i);
          if (data[rowNum].ch_type == 13) {
            hot.setCellMeta(i,item.col,"readOnly",false);
          } else {
            hot.setCellMeta(i,item.col,"readOnly",true);
           }
        }
      }
       /* tms */
      if (item.prop=='baseInfo.speedRate') {
        for (let i = 0; i < rows; i++) { 
          let rowNum = hot.toPhysicalRow(i);
          if (data[rowNum].dgm_type == 7) {
            hot.setCellMeta(i,item.col,"readOnly",true);
          } else {
            hot.setCellMeta(i,item.col,"readOnly",false);
          }
        }
      }
    }
     /*  */
     if (type == 3) {
       if (item.col >= 8) {//WL9100 
          for (let i = 0; i < rows; i++) { 
            let rowNum = hot.toPhysicalRow(i);
              if (data[rowNum].dgm_type == 10) {
                hot.setCellMeta(i,item.col,"readOnly",true);
              }
              else{
                hot.setCellMeta(i,item.col,"readOnly",false);
                if(item.col == 10 || item.col == 11){
                  hot.setCellMeta(i,item.col,"readOnly",true);
                }
                if (item.col >= 13) {//MHD 
                  if (data[rowNum].dgm_type == 4) {
                    hot.setCellMeta(i,item.col,"readOnly",true);
                  }
                }
             }
          }
        }
     }
     /*  */
     if (type == 5) {
       /* V1 */
       if (item.col == 7 || item.col == 8 || item.col == 9) {
        for (let i = 0; i < rows; i++) {
          let rowNum = hot.toPhysicalRow(i);
          if (data[rowNum].dgm_type == 1 || data[rowNum].dgm_type == 2) {
            hot.setCellMeta(i,item.col,"readOnly",true);
          } else {
            hot.setCellMeta(i,item.col,"readOnly",false);
          }
        }
       }
     }
     /*   */
     if (type == 6 || type == 12) {
       if (item.col == 3 || item.col == 4 || item.col == 5) { //  
          for (let i = 0; i < rows; i++) { 
            hot.setCellMeta(i,item.col,"readOnly",true);
          }
       }
       //MHD
       if (item.col  >= 14) {
        for (let i = 0; i < rows; i++) { 
          let rowNum = hot.toPhysicalRow(i);
          if(data[rowNum].dgm_type == 4){
            hot.setCellMeta(i,item.col,"readOnly",true);
          }else{
            hot.setCellMeta(i,item.col,"readOnly",false);
          }
        }
      }
     }
     /*  */
     if (type == 11) {
       //MHD
       if (item.col >= 22) {
        for (let i = 0; i < rows; i++) { 
          let rowNum = hot.toPhysicalRow(i);
          if(data[rowNum].dgm_type == 4){
            hot.setCellMeta(i,item.col,"readOnly",true);
          } else {
            hot.setCellMeta(i,item.col,"readOnly",false);
          }
        }
       }
     }
     /*  */
     if (type == 7) {
       //MHD
       if (item.prop=='isalarm_val') {
          for (let i = 0; i < rows; i++) { 
            let rowNum = hot.toPhysicalRow(i);
            if (data[rowNum].dgm_type == 7) {
              hot.setCellMeta(i,item.col,"readOnly",true);
            } else {
              hot.setCellMeta(i,item.col,"readOnly",false);
            }
          }
       }
     }
     /*  */
     if (type == 17) {
       //
       if (item.prop=='ch_name') {
          for (let i = 0; i < rows; i++) { 
            let rowNum = hot.toPhysicalRow(i);
            if (data[rowNum].operate == 1) {
              hot.setCellMeta(i,item.col,"readOnly",false);
            } 
            else if (data[rowNum].operate == 0) {
              hot.setCellMeta(i,item.col,"readOnly",true);
            }
          }
       }
     }
     /*  */
     if (type == 13) {
       //18X 23Y,1320X 21 25Y26Y
       if (item.col == 18 || item.col == 23||item.col == 13 || item.col == 20 || item.col == 21 || item.col == 25 || item.col == 26) {
        for (let i = 0; i < rows; i++) { 
          let rowNum = hot.toPhysicalRow(i);
          if (data[rowNum].baseInfo.sourceType == 0) {
            hot.setCellMeta(i,item.col,"readOnly",true);
          } else {
            hot.setCellMeta(i,item.col,"readOnly",false);
          }
        }
       }
       /* MHD */
        if (item.col >= 28) {
          for (let i = 0; i < rows; i++) { 
            let rowNum = hot.toPhysicalRow(i);
            if(data[rowNum].dgm_type == 4  ){
              hot.setCellMeta(i,item.col,"readOnly",true);
            } else {
              hot.setCellMeta(i,item.col,"readOnly",false);
            }
          }
        }
     }
     /*  */
     if (type == 200) {
      for (let i = 0; i < rows; i++) { 
        let rowNum = hot.toPhysicalRow(i);
         /*  */
        if (item.col == 4) { //
          if (data[rowNum].operate == 1) {
            hot.setCellMeta(i,item.col,"readOnly",false);
          } else if (data[rowNum].operate == 0) {
            hot.setCellMeta(i,item.col,"readOnly",true);
        }
        }
        /*  */
        if (data[rowNum].baseInfo.capture_mode == 0) {
          if (item.col == 6) {/*  */
            hot.setCellMeta(i,item.col,"readOnly",false);
          }
          if (item.col == 7) {/*  */
            hot.setCellMeta(i,item.col,"readOnly",true);
          }
        }
        /*  */
        if (data[rowNum].baseInfo.capture_mode == 1) {
          if (item.col == 6) {/*  */
            hot.setCellMeta(i,item.col,"readOnly",true);
          }
          if (item.col == 7) {/*  */
            hot.setCellMeta(i,item.col,"readOnly",false);
          }
        }
      }
     }
     /*  */
     if (type == 18) {
        //
        if (item.prop=='isalarm_val') {
           for (let i = 0; i < rows; i++) { 
            hot.setCellMeta(i,item.col,"readOnly",true);
          }
        }
     }
     /*tms*/
     if(tmsPosList.includes(type) && item.prop == 'baseInfo.if_show_val'){
      for (let i = 0; i < rows; i++) { 
        let rowNum = hot.toPhysicalRow(i);
        if(data[rowNum].dgm_type != 7){//tms
          hot.setCellMeta(i,item.col,"readOnly",true);
        } else {
          hot.setCellMeta(i,item.col,"readOnly",false);
        }
      }
     }
  })
}
