/**
   * *
   * dgm_type
   * posType
   * filter0,1,2
   *
   * **/
function frequencySources(position, filter) {
  let dgm_type = position.dgm_type
  let posType = position.pos_type
  let frequency_sources = {};
  if (dgm_type == 1) {//dgu2000
    frequency_sources = { '10': '10', '20': '20', '50': '50', '100': '100', '200': '200', '500': '500', '1000': '1K', '2000': '2K', '5000': '5K', '10000': '10K' };
    if(posType == 4){//
      /*  */
      if (filter == 0) {
        frequency_sources = { '10': '10', '20': '20', '50': '50', '100': '100', '200': '200', '500': '500'};
      }
      else if(filter == 1){
        frequency_sources = { '10': '10', '20': '20', '50': '50', '100': '100', '200': '200', '500': '500', '1000': '1K', '2000': '2K'};
      }
      else if(filter == 2){
        frequency_sources = {'50': '50', '100': '100', '200': '200', '500': '500', '1000': '1K', '2000': '2K', '5000': '5K', '10000': '10K' };
      }
    }
  }
  if (dgm_type == 2) {
    frequency_sources = { '10': '10', '20': '20', '50': '50', '100': '100', '200': '200', '500': '500', '1000': '1K', '2000': '2K'};
  }
  else if (dgm_type == 3 || dgm_type == 7 || dgm_type == 9 || dgm_type == 10 || dgm_type == 13) {//v2tmsDGU2000EWL9100YHC5000
    if (posType == 3 || posType == 6 || posType == 11 || posType == 12 || posType == 13 || posType == 15) {//,
      frequency_sources = { '10': '10', '20': '20', '50': '50', '100': '100', '200': '200', '500': '500', '1000': '1K', '2000': '2K', '4000': '4K', '8000': '8K', '16000': '16K', '32000': '32K' };
      if(dgm_type == 3){//dgu2000v25k,10k
        frequency_sources = { '10': '10', '20': '20', '50': '50', '100': '100', '200': '200', '500': '500', '1000': '1K', '2000': '2K', '4000': '4K', '5000': '5K', '8000': '8K', '10000':'10k', '16000': '16K', '32000': '32K' };
      }
      /*v2DGU2000E*/
      if(config.company===1 && (dgm_type == 3 || dgm_type == 9)){
        frequency_sources = { '10': '10', '20': '20', '50': '50', '100': '100', '200': '200', '500': '500', '1000': '1K', '2000': '2K', '4000': '4K', '8000': '8K', '10000':'10k','16000': '16K', '32000': '32K' };
      }
    }
    else if (posType == 4) {//,
      /*  */
      if (filter == 0) {
        frequency_sources = { '12': '12.5', '25': '25', '50': '50', '100': '100', '200': '200' };
      }
      else if (filter == 1) {
        frequency_sources = { '12': '12.5', '25': '25', '50': '50', '100': '100', '200': '200', '400': '400', '800': '800', '1600': '1.6K', '3200': '3.2K' };
      }
      else if (filter == 2) {
        frequency_sources = { '12': '12.5', '25': '25', '50': '50', '100': '100', '200': '200', '400': '400', '800': '800', '1600': '1.6K', '3200': '3.2K', '6400': '6.4K', '12800': '12.8K' };
      }
    } else if (posType == 8 || posType == 16 || posType == 9) {//
      frequency_sources = { '1': '1', '2': '2', '4': '4', '8': '8', '16': '16', '32': '32', '64': '64', '128': '128', '256': '256', '512': '512', '1024': '1024' }
    }
  }
  else if (dgm_type == 4) {
    frequency_sources = { '10': '10', '20': '20', '50': '50', '100': '100', '200': '200', '500': '500', '1000': '1K', '2000': '2K', '4000': '4K' };
  }
  if (posType == 13 && position.baseInfo.sourceType == 5) {/* 20 */
    frequency_sources = { '20': '20' }
  }
  if (posType == 12 && dgm_type == 0) {/* 20 */
    frequency_sources = { '20': '20' }
  }
  return frequency_sources;
}

/**
   * *
   * dgm_type
   *
   * **/
function spectralSources(dgm_type, pos_type) {
  let spectral_source = [];
  if (dgm_type == 3 || dgm_type == 7  || dgm_type == 9 || dgm_type == 10 || dgm_type == 13) {
    if (pos_type == 8 || pos_type == 9 || pos_type == 16) {
      spectral_source = ['32', '64', '128', '256', '512', '1024', '2048']
    } else {
      spectral_source = ['100', '200', '400', '800', '1600', '3200', '6400', '12800', '25600', '51200'];
      /* */
      if(config.company===1){
        spectral_source = ['100', '200', '400', '800', '1600', '3200', '6400', '12800', '19200','25600', '51200','102400','204800','409600'];
      }
    }
  }
  else if (dgm_type == 4) {
    spectral_source = ['100', '200', '400', '800', '1600', '3200']
  }
  else if (dgm_type == 1){
    spectral_source = ['100', '200', '400', '800', '1600', '3200', '6400', '12800', '25600', '51200', '102400'];
    if(pos_type == 13){//v1100/200
      spectral_source = ['400', '800', '1600', '3200', '6400', '12800', '25600', '51200', '102400'];
    }
  }
  else if(Number(dgm_type) === 0){
    if(pos_type == 13){//100/200
      spectral_source = ['400', '800', '1600', '3200', '6400', '12800', '25600', '51200', '102400'];
    }
    else{
      spectral_source = ['100', '200', '400', '800', '1600', '3200', '6400', '12800', '25600', '51200', '102400']
    }
  }
  else {
    spectral_source = ['100', '200', '400', '800', '1600', '3200', '6400', '12800', '25600', '51200', '102400']
  }
  return spectral_source;
}
/*  */
function computed__frequency_spectral(position) {
  /*  */
  let frequency, spectral
  if (position.dgm_type == 2) {
    frequency = position.baseInfo.frequency * 2.5;
    spectral = position.baseInfo.spectral * 2.56;
  } else {
    frequency = position.baseInfo.frequency
    spectral = position.baseInfo.spectral
  }
  position.baseInfo.order = (spectral / frequency).toFixed(2) + "s";
  position.baseInfo.specOrder = (frequency / spectral).toFixed(4) + "Hz";
}

/**
 * *
 * **/
function taskId(val) {
  let taskIdVal = "";
  switch (parseInt(val)) {
    case 0:
      taskIdVal = "1";
      break;
    case 1:
      taskIdVal = "2";
      break;
    case 2:
      taskIdVal = "3";
      /*'3'''*/
      if(config.company===1){
        taskIdVal = "";
      }
      break;
  }
  return taskIdVal;
}
/*  */
function taskArray(arr) {
  if (!Array.isArray(arr) || arr.length == 0) {
    return []
  } else {
    let result = []
    arr.sort()
    arr.forEach(el => {
      result.push(taskId(parseInt(el)))
    })
    return result
  }
}


export { posType_name, frequencySources, spectralSources, taskId, taskArray, computed__frequency_spectral, addRows, reRenderSelectOption }
